import { useState, useEffect } from "react";
import Home from "./Home";
import Projects from "./Projects";
import About from "./About";
import Resume from "./Resume";
import styles from "../css/Content.module.scss";
import "../css/transitions.scss";
import { CSSTransition } from "react-transition-group";

const Content = ({ page, className, setpage }) => {
  // const [pageStyle, setpageStyle] = useState(
  //   window.innerWidth > 768
  //     ? page
  //     : { home: true, projects: true, about: true, resume: true }
  // );
  const [type, settype] = useState(window.innerWidth >= 768 ? true : false);
  const handleWindowChange = () => {
    if (window.innerWidth < 768 && type === true) {
      setpage({ home: true, projects: true, about: true, resume: true });
      settype(false);
    } else if (window.innerWidth >= 768 && type === false) {
      settype(true);
      setpage({ home: true, projects: false, about: false, resume: false });
    }
  };
  useEffect(() => {
    window.addEventListener("resize", handleWindowChange);
    return () => {
      window.removeEventListener("resize", handleWindowChange);
    };
  }, [type]);
  return (
    <div className={className}>
      <div id="top"></div>
      <div className={styles.header}>
        <p className={styles.title}>Nathan Yik</p>
        <nav className={styles.links}>
          <a href="#top" className={styles.link}>
            Home
          </a>
          <a href="#projects" className={styles.link}>
            Projects
          </a>
          <a href="#about" className={styles.link}>
            About
          </a>
          <a href="#resume" className={styles.link}>
            Resume
          </a>
        </nav>
      </div>
      <CSSTransition
        in={page.home}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Home />
      </CSSTransition>
      <CSSTransition
        in={page.projects}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Projects />
      </CSSTransition>
      <CSSTransition
        in={page.about}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <About />
      </CSSTransition>
      <CSSTransition
        in={page.resume}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Resume />
      </CSSTransition>
    </div>
  );
};

export default Content;
