import { useState, useEffect } from "react";
import pic from "../Media/Warframe0040.jpg";
import styles from "../css/Sidebar.module.scss";
const Sidebar = ({ setpage, className, selfclosing, setvisible }) => {
  const [type, settype] = useState(window.innerWidth >= 768 ? true : false);
  const handleWindowChange = () => {
    if (window.innerWidth < 768 && type === true) {
      settype(false);
      setvisible(true);
    } else if (window.innerWidth >= 768 && type === false) {
      settype(true);
      setvisible(false);
    }
  };
  useEffect(() => {
    window.addEventListener("resize", handleWindowChange);
    return () => {
      window.removeEventListener("resize", handleWindowChange);
    };
  }, [type]);
  return (
    <div className={className}>
      <div className={styles.padding}></div>
      <a
        href="#top"
        className={styles.btn}
        onClick={() => {
          if (selfclosing) {
            setvisible(true);
          }
          setpage({
            home: true,
            projects: false,
            about: false,
            resume: false,
          });
        }}
      >
        Home
      </a>
      <a
        href="#top"
        className={styles.btn}
        onClick={() => {
          if (selfclosing) {
            setvisible(true);
          }
          setpage({
            home: false,
            projects: true,
            about: false,
            resume: false,
          });
        }}
      >
        Projects
      </a>
      <a
        href="#top"
        className={styles.btn}
        onClick={() => {
          if (selfclosing) {
            setvisible(true);
          }
          setpage({
            home: false,
            projects: false,
            about: true,
            resume: false,
          });
        }}
      >
        About
      </a>
      <a
        href="#top"
        className={styles.btn}
        onClick={() => {
          if (selfclosing) {
            setvisible(true);
          }
          setpage({
            home: false,
            projects: false,
            about: false,
            resume: true,
          });
        }}
      >
        Resume
      </a>
    </div>
  );
};

export default Sidebar;
